	module.exports = {"data":{"challengeNode":{"title":"Hailstone sequence","description":["<p>The Hailstone sequence of numbers can be generated from a starting positive integer,  n  by:</p>"," If  n  is   1   then the sequence ends."," If  n  is  even then the next  n  of the sequence <code> = n/2 </code>"," If  n  is  odd   then the next  n  of the sequence <code> = (3 * n) + 1 </code><p>The (unproven) <a href=\"https://en.wikipedia.org/wiki/Collatz conjecture\" title=\"wp: Collatz conjecture\">Collatz conjecture</a> is that the hailstone sequence for any starting number always terminates.</p>","<p>The hailstone sequence is also known as hailstone numbers (because the values are usually subject to multiple descents and ascents like hailstones in a cloud), or as the Collatz sequence.</p>","Task:","Create a routine to generate the hailstone sequence for a number.","Use the routine to show that the hailstone sequence for the number 27 has 112 elements starting with <code>27, 82, 41, 124</code> and ending with <code>8, 4, 2, 1</code>","Show the number less than 100,000 which has the longest hailstone sequence together with that sequence's length.   (But don't show the actual sequence!)See also:"," <a href=\"http://xkcd.com/710\" title=\"link: http://xkcd.com/710\">xkcd</a> (humourous)."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/rosetta-code/hailstone-sequence","blockName":"Rosetta Code","tests":[{"text":"<code>hailstoneSequence</code> is a function.","testString":"assert(typeof hailstoneSequence === 'function', '<code>hailstoneSequence</code> is a function.');"},{"text":"<code>hailstoneSequence()</code> should return <code>[[27,82,41,124,8,4,2,1], [351, 77031]]</code>","testString":"assert.deepEqual(hailstoneSequence(), res, '<code>hailstoneSequence()</code> should return <code>[[27,82,41,124,8,4,2,1], [351, 77031]]</code>');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"// noprotect\nfunction hailstoneSequence () {\n  const res = [];\n  // Good luck!\n\n  return res;\n}","head":"","tail":"const res = [[27, 82, 41, 124, 8, 4, 2, 1], [351, 77031]];"},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/rosetta-code/happy-numbers","id":"595608ff8bcd7a50bd490181"},"slug":"/coding-interview-prep/rosetta-code/hailstone-sequence"}}

